.new-deals {
    display: flex; 
    flex-direction: column;
    align-items: center;
    gap: 20px;
    margin-bottom: 100px;
}

.new-deals h3 {
    font-size: 30px;
    font-weight: 600;
}

.new-deals hr {
    width: 60px;
    height: 6px;
    border-radius: 10px;
    background: #252525;
}

.deals {
    margin-top: 50px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 30px;

}